var {log} = console
// 引入提示组件
import HMmessages from "@/components/HM-messages/HM-messages.vue"
// post
import {publicing} from '../../api/api.js'
// 地址
import {wxpayingurl} from '../../api/request.js'
export default{
	components: {HMmessages},
	data() {
		return {
			addreing: true,
			allorder:[],
			openid:'',
			payment:'',
			physical:Number,
			MerchantId:'',
			nameshop:'',
			logo:'',
			// 收货地址
			addressing:{}
		}
	},
	
	methods:{
		// 选择收货地址
		addRess(){
			wx.chooseAddress({
				success:(res)=>{
					log(res)
					this.addressing = res
					this.addreing = false
				}
			})
		},
		
		// 去支付
		toPay(){
			if(this.addreing == true){
				log('没有选择收货地址')
				let succ = '请选择收货地址'
				let ico = 'error'
				this.tIps(succ,ico)
				return false
			}
			log('可以支付')
			this.wxPay()
		},
		
		// 微信支付
		async wxPay(){
			// 提示用户正在下单，防止用户多次点击下单按钮
			wx.showToast({
			  title: '正在下单',
			  icon: 'loading',
			  duration:200000,
			  mask:true
			})
			// 搞定必填参数
			// 客户信息
			let peopleobj = {
				address:this.addressing.detailInfo,
				name:this.addressing.userName,
				iphone:this.addressing.telNumber
			}
			// 商家标识
			let merchantid = this.MerchantId
			// 截取商家标识字符串
			let ide = this.MerchantId.slice(0,7)
			// 商家名称
			let commod = this.nameshop
			// 商家logo
			let logo = this.logo
			// 把要发送到后台的数据以对象形式存储
			let Paymentinfor = {
				type:'placeOrder',
				peopleobj,
				arrinfo:this.allorder,
				merchantid,
				ide,
				commod,
				logo,
				useropenid:this.openid,
				payment:this.payment
			}
			// log(Paymentinfor)
			// es6 :async await:异步编程同步化
			// 第一步：统一下单
			// await:等待
			let Placeorder = await this.placeOrder(Paymentinfor)
			log(Placeorder) 
			let Payparame = Placeorder.data
			
			// 第二步：发起支付
			let wxpay = await this.wxPays(Payparame)
			log(wxpay)
			// 第三步：查询时候支付成功
			let paysucc = await this.paySucc(Payparame)
		},
		
		
		// 第一步：统一下单
		placeOrder(Paymentinfor){
			return new Promise((resolve,reject)=>{
				publicing(wxpayingurl,Paymentinfor)
				.then((res)=>{
					// log(res)
					wx.hideToast()
					resolve(res)
				})
				.catch((err)=>{
					log(err)
					wx.hideToast()
					reject('支付错误')
				})
			})
		},
		
		//第二步：发起支付
		wxPays(Payparame){
			return new Promise((resolve,reject)=>{
				wx.requestPayment({
					timeStamp:Payparame.time_stamp,
					nonceStr:Payparame.nonceStr,
					package:`prepay_id=${Payparame.prepayId}`,
					signType:'MD5',
					paySign:Payparame.payauto,
					success:(res)=>{
						// log(res)
						resolve(res)
					},
					fail:(err)=>{
						log(err)
						reject(err)
						let succ = '支付失败'
						let ico = 'success'
						this.tIps(succ,ico)
					}
				})
			})
		},
		
		
		// 查询是否支付成功
		paySucc(Payparame){
			return new Promise((resolve,reject)=>{
				let data = {
					type:'paysucc',
					out_trade_no:Payparame.out_trade_no,
					id:Payparame._id,
					merchantid:this.MerchantId
				}
				publicing(wxpayingurl,data)
				.then((res)=>{
					log(res)
					let succ = '支付成功'
					let ico = 'success'
					this.tIps(succ,ico)
				})
				.catch((err)=>{
					log(err)
					let succ = '支付失败'
					let ico = 'success'
					this.tIps(succ,ico)
				})
			})
		},
		
		
		
		
		
		
		// 提示
		tIps(succ,ico){
			this.HMmessages.show(succ,{icon:ico,fontColor:"#ffffff",iconColor:"#ffffff",background:"rgba(102,0,51,0.8)"})
		}
	},
	
	// 接收下单页面的路径数据
	onLoad(e) {
		log(e)
		let ordermen = JSON.parse(e.ide)
		log(ordermen)
		// 商品数据
		this.allorder = ordermen.allorder
		// openid
		this.openid = ordermen.openid
		// 支付的价格
		this.payment = ordermen.payment
		// 配送费
		this.physical = ordermen.physical
		// 商家标识
		this.MerchantId = ordermen.MerchantId
		// 商家名称
		this.nameshop = ordermen.nameshop
		// 商家logo
		this.logo = ordermen.logo
	}
	
}
